import Vue from 'vue'
import Router from 'vue-router'

import NewIndex from '@/components/NewIndex'




import Gift from "../components/Gift";


//import datePicker from "../../static/datePicker.js"

//Vue.use(datePicker)
Vue.use(Router)

export default new Router({

  routes: [
    {
      path: '/',
      name: 'NewIndex',
      component: NewIndex
    },
    {
      path: '/gift',

      name: 'Gift',
      component: Gift
    }


  ]
})



