<template>
    <div id="index">
      <div class="top">
        <img src="../assets/bannerred.gif" alt="">
        <img class="shouLine" src="../assets/snowline.png" alt="">
      </div>
     <ul class="box">
       <li><input type="text" v-model="name" placeholder="请输入姓名"></li>
       <li><input type="text" v-model="phone" placeholder="请输入手机号"></li>
       <li class="code"><input type="text" v-model="code" placeholder="请输入验证码"><span @click="getCode" class="codebtn">{{codename}}</span></li>

     </ul>
      <button class="btn" @click="submit">领取惊喜</button>
    </div>

</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
    export default {
        name: "NewIndex",
        data(){
          return{
            name:"",
            phone:"",
            code:"",
            realCode:"",
            codename:"获取验证码",
            lists:[]
          }
        },
      created(){
      this.getList()
      },
      methods:{
          submit(){

          if(this.name==''){
            alert('姓名不能为空')
            return false;
          }
          if(this.phone==''){
            alert('手机号不能为空')
            return false;
          }
          if(this.code!=this.realcode){
            alert('验证码不正确')
            return false;
          }
          var list=this.lists.find((item,index)=>{
            return this.name==item.name&&this.phone==item.phone
          })
            if(list){
              this.$router.push({path:'/gift',query: {name:this.name,password:list.password,bless:list.bless}})
            }else {
              alert('没有您的相关信息')
            }
            console.log(list)
          },
          getList(){
            axios.get("/hlo/static/person.json").then((data)=>{
              console.log(data)
              this.lists=data.data
            })
          },
        getCode() {
          var myreg = /^(14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9])\d{8}$$/;
          if (this.phone == '') {
           alert('手机号不能为空')
            return false;
          } else if (!myreg.test(this.phone)) {
            alert('手机号不能为空')
            return false;
          } else {
           var backData={phone:this.phone}
           console.log(backData)
            axios.post("https://ci.huimeicare.com/center/account/sendSmsAJAX.do",qs.stringify(backData)).then((data)=>{
              if(data.data.code==200){
                this.realcode=data.data.data
              }

              console.log(data)
            })


            var num = 60;
            var timer = setInterval(() => {
              num--;
              if (num <= 0) {
                clearInterval(timer);
                this.codename='重新发送'
                /*this.setData({
                  codename: '重新发送',
                  disabled: false
                })*/

              } else {
                this.codename=num+"s"
                /*this.setData({
                  codename: num + "s",
                  disabled: true


                })*/
              }
            }, 1000)


          }
        },
      }


    }
</script>

<style scoped>
.top img{
  width: 100%;
}
.top .shouLine{
  margin-top: -0.8rem;
}
.box{
  padding-top: 0.2rem;
  margin: 0 auto;
  width: 80%;
}
.box li{
  border: 1px solid #d59d2b;
 margin-bottom: 0.5rem;
  border-radius: 8px;
  background: rgba(213,157,43,.3);
}
.box li.code{
  display: flex;

}
.box li input{
  width: 100%;
  height: 0.94rem;
  /*line-height: 0.94rem;*/
  border-radius: 8px;
  box-sizing: border-box;
}
input{
  padding: 0.28rem 0.3rem;
  outline: none;
  -webkit-appearance: none;
 border: none;
  background: none;
  color: #fff;
  font-size: 0.28rem;
}
  .btn{
    display: block;
    margin: 0.7rem auto 0;
    width: 4.2rem;
    height: 0.9rem;
    background:#e2b341;
    border-radius: 8px;
    font-size: 0.28rem;
    color: #8c0018;

    background:-webkit-linear-gradient(left,#d59e2c,#efca58);
    border: none;
    font-weight: bold;
    text-align: center;
    box-shadow: 1px 1px 5px rgba(0,0,0,.5);
  }
  .codebtn{
    margin-top: 0.2rem;
    margin-right: 0.2rem;
    width: 40%;
    background: rgba(255,255,255,.3);
    color: #fff;
    font-size: 0.26rem;
    height: 0.54rem;
    line-height: 0.54rem;
    text-align: center;
  }
</style>



